import React, { useState } from 'react';
import CV from '../components/CV';
import omma_exhi_1 from '../asset/exhi/omma_exhi_1.jpg';
import omma_exhi_2 from '../asset/exhi/omma_exhi_2.jpg';
import omma_exhi_3 from '../asset/exhi/omma_exhi_3.jpg';
import penet_exhi_2 from '../asset/exhi/penet_exhi_2.jpg';
import penet_exhi_4 from '../asset/exhi/penet_exhi_4.jpg';
import penet_exhi_5 from '../asset/exhi/penet_exhi_5.jpg';
import eter_exhi_1 from '../asset/exhi/eter_exhi_1.jpg';
import eter_exhi_2 from '../asset/exhi/eter_exhi_2.jpg';
import eter_exhi_3 from '../asset/exhi/eter_exhi_3.jpg';
import '../style/web/exhibi.css';
import '../style/mobile/exhibi_m.css';


const Exhibi = () =>  {
  const EXHI_RECK = [
    {
      detailExhi:[
        omma_exhi_1,
        omma_exhi_2,
        omma_exhi_3,
      ],
      title: " ",
      author: "()",
      paper:
      <p>
        'Put your left hand on the stones'<br/><br/>


                     .               .                     . ..             .          .     ,   .    ,                             .<br/><br/><br/>


             2019 ,  .            .           .           .                .           .    Muh Emdap Inam Mo'      ,     .         .<br />
                 .  , MDF   ,     .        .            .               . ,         .                  .       .                 .<br />
               .         .                         .        ,       .     ,     ,                .<br /><br />
        
                      .  ,                        .    ?      ?         . <br />
      </p>
    },
    {
      detailExhi:[
        penet_exhi_2,
        penet_exhi_4,
        penet_exhi_5,
      ],
      title: " ",
      author: "(, )",
      paper:
      <p>
        
        Penetrating Stone             .      ,    ( )     ,       ,           .                  .<br /><br />
            
           ,         .      1, 2, 3    ,    1           .                    .                .            .                  .   ,     ,  .<br /><br />
            
         2     .  1  20 1, , ,        ()  .  (canon)     (parody)  2  (1)   . 2   400               . 1     2  3 .   2   , 1~3   (pictorial continuum)     .            (mirroring)  . <br /><br />
            
           23 1        .           .      ,          ?         ?<br /><br />
            
                .            .                      .           .            ,           .   3         . <br /><br />
            
                 ,  2 1        .      1   ,      .            ,      .    2             . 2            .<br /><br /> 
            
        3 2  1      .     2    , 1        .  3 1    ,             ,           .  2  1      . 1    2        , 3  2   .        ?         .     .        (simulacre).<br /><br />
            
                        .      ,     . <br /><br />
  
      </p>
    },
    {
      detailExhi:[
        eter_exhi_1,
        eter_exhi_2,
        eter_exhi_3,
      ],
      title: "()",
      author: "",
      paper:
      <p>
                    .         ()  ,         (),  (), (), ()         .         ,     .     ,              .                   . <br /><br />

             ()    .   , ,          , ,           .   , ,     ,     (),  (),   (,   ).          ,          .  20       ,             .                 ,             . <br /><br />

                    .  ,  ,    ,       .           ,               .       ,        ,         .            .           ,     . <br /><br />

                        .                 ,            .             ,       . <br /><br />

               ,        .         ,                   .         ,                .                ,            .
      </p>
    },
  ];

  const [exhibiOmma,setExhibiOmma] = useState(0);
  const [exhibiPenet,setExhibiPenet] = useState(0);
  const [exhibiEter,setExhibiEter] = useState(0);

  const shiftExhibiOmma = (e) => {
    const {target: {value}} = e;
    if(value === "prev" && exhibiOmma !== 0){
      setExhibiOmma(exhibiOmma -1);
    } else if(value === "next" && exhibiOmma !== 2){
      setExhibiOmma(exhibiOmma +1);
    }
  };

  const shiftExhibiPenet = (e) => {
    const {target: {value}} = e;
    if(value === "prev" && exhibiPenet !== 0){
      setExhibiPenet(exhibiPenet -1);
    } else if(value === "next" && exhibiPenet !== 2){
      setExhibiPenet(exhibiPenet +1);
    }
  };

  const shiftExhibiEter = (e) => {
    const {target: {value}} = e;
    if(value === "prev" && exhibiEter !== 0){
      setExhibiEter(exhibiEter -1);
    } else if(value === "next" && exhibiEter !== 2){
      setExhibiEter(exhibiEter +1);
    }
  };
  // i _CV
  const [exhibiCV,setExhibiCV] = useState(false)
  const showexhibiCV = (e) => {
    setExhibiCV(!exhibiCV)
    console.log(exhibiCV);
  }


  return (
    <section className="section_exhibi">
      <div className="exhibi_title">
        <h1>Exhibitions</h1>
        <button onClick={showexhibiCV}>&#10094;</button>
      </div>
      <div className="exhibi_reck">
        
          <div className="eachExhibi">
            <img className="eachExhibi_img" src={EXHI_RECK[0].detailExhi[exhibiOmma]} alt="exhi detail"/>
            <div className="eachExhibi_btn">
              <button className="btn" value="prev" onClick={shiftExhibiOmma}>&#10094;</button>
              <button className="btn" value="next" onClick={shiftExhibiOmma}>&#10095;</button>
            </div>
            <div className="eachExhibi_Paper">
              <h1>{EXHI_RECK[0].title}</h1>
              <h3>{EXHI_RECK[0].author}</h3>
              {EXHI_RECK[0].paper}
            </div>
          </div>


          <div className="eachExhibi">
            <img className="eachExhibi_img" src={EXHI_RECK[1].detailExhi[exhibiPenet]} alt="exhi detail"/>
            <div className="eachExhibi_btn">
              <button className="btn" value="prev" onClick={shiftExhibiPenet}>&#10094;</button>
              <button className="btn" value="next" onClick={shiftExhibiPenet}>&#10095;</button>
            </div>
            <div className="eachExhibi_Paper">
              <h1>{EXHI_RECK[1].title}</h1>
              <h3>{EXHI_RECK[1].author}</h3>
              {EXHI_RECK[1].paper}
            </div>
          </div>


          <div className="eachExhibi">
            <img className="eachExhibi_img" src={EXHI_RECK[2].detailExhi[exhibiEter]} alt="exhi detail"/>
            <div className="eachExhibi_btn">
              <button className="btn" value="prev" onClick={shiftExhibiEter}>&#10094;</button>
              <button className="btn" value="next" onClick={shiftExhibiEter}>&#10095;</button>
            </div>
            <div className="eachExhibi_Paper">
              <h1>{EXHI_RECK[2].title}</h1>
              <h3>{EXHI_RECK[2].author}</h3>
              {EXHI_RECK[2].paper}
            </div>
          </div>
      </div>
      {
      exhibiCV ? <CV showDetail={showexhibiCV} />
      :(
      <></>
      )}
    </section>
  );
}

export default Exhibi;

